.contactHero {
  position: relative;
  min-height: 100vh;

  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.contactHero__scrim {
  position: absolute;
  inset: 0;
  background: linear-gradient(
    90deg,
    rgba(0, 59, 118, 0.55),
    rgba(30, 25, 106, 0.25)
  );
}

.contactHero__content {
  position: relative;
  z-index: 1;

  margin: 0 auto;
  height: 100vh;
  padding: clamp(16px, 3vw, 32px);
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.contactHero__card {
  width: min(560px, 92%);
  background: #ffffff;
  border-radius: 18px;
  padding: 24px;
  box-shadow: 0 30px 60px rgba(0, 0, 0, 0.3);
}

.contact__title {
  margin: 2px 0 14px;
  font-weight: 800;
  font-size: clamp(28px, 4vw, 40px);
  line-height: 1.05;
  color: #111827;
}
.contact__form {
  display: grid;
  gap: 12px;
}

.contact__input,
.contact__textarea {
  width: 100%;
  border: 1px solid #e5e7eb;
  border-radius: 10px;
  padding: 12px 14px;
  font-size: 15px;
  background: #f4f6ff;
  outline: none;
}
.contact__input:focus,
.contact__textarea:focus {
  border-color: #6366f1;
  box-shadow: 0 0 0 3px rgba(99, 102, 241, 0.25);
  background: #fff;
}

.contact__btn {
  margin-top: 6px;
  padding: 12px 14px;
  border: none;
  border-radius: 10px;
  background: #4f46e5;
  color: #fff;
  font-weight: 700;
  font-size: 16px;
  cursor: pointer;
}
.contact__btn:disabled {
  opacity: 0.7;
  cursor: not-allowed;
}

.contact__alert {
  margin: 8px 2px 0;
  font-size: 14px;
}
.contact__alert--error {
  color: #b91c1c;
}
.contact__alert--success {
  color: #065f46;
}

@media (max-width: 900px) {
  .contactHero__content {
    justify-content: center;
  }
}
.brands__rule {
  border: none;
  height: 3px;
  background: rgba(255, 255, 255, 0.75);
  margin: 8px 0 14px;
}
